.text {
  margin: 0;
  font-family: var(--second-font);
  font-weight: 300;
  font-style: normal;
  color: var(--main-dark-color);

  &_accent {
    font-family: var(--main-font);
    font-weight: 400;
  }

  &_size {
    &_xl {
      font-size: 28px;
      line-height: 32px;
    }

    &_l {
      font-size: 20px;
      line-height: 26px;
    }

    &_m {
      font-size: 16px;
      line-height: 22px;
    }

    &_s {
      font-size: 14px;
      line-height: 17px;
    }
  }
}
